import { Box, Typography } from "@mui/material"
import * as Cores from "../../utils/cores"


export default function Item(props) {
    const { item, selecionarItem, titulo, index } = props

    return (

        <Box key={index} onClick={() => selecionarItem(titulo)} sx={{marginInline: '16px', cursor: 'pointer'}}>
            <Typography fontWeight={600} sx={{ color: titulo === item ? 'white' :  Cores.COR_LETRA_SEM_DESTAQUE_TOOLBAR  }}>{titulo}</Typography>
        </Box >
    )
}