import { Box } from "@mui/material";
import BossFightGif from "../../img/bossfight.gif";
import EggGif from "../../img/carry_egg.gif";
import * as Cores from "../../utils/cores";

export default function GifArea(props) {

    return (
        <Box sx={{ background: Cores.COR_PRINCIPAL, padding: '64px', marginTop: '-4px' }}>
            <Box>
                
                <Box>
                    <img src={BossFightGif} alt="Boss" style={{ height: '350px' }} />
                </Box>
            </Box>
            <Box>
                <img src={EggGif} alt="Boss" style={{ height: '350px' }} />
            </Box>
        </Box>
    )
}