import { Box, Typography } from "@mui/material";
import P1 from "../../img/p1.png";
import P2 from "../../img/p2.png";
import P1Full from "../../img/p1full.png";
import P2Full from "../../img/p2full.png";
import { useState } from 'react'

const personagens = [{
    img: P1, imgFull: P1Full,
    texto: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industrys standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.',
    nome: 'Hector'
}, {
    img: P2, imgFull: P2Full,
    texto: 'There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which dont look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isnt anything embarrassing hidden in the middle of text. All the Lorem Ipsum generators on the Internet tend to repeat predefined chunks as necessary, making this the first true generator on the Internet. It uses a dictionary of over 200 Latin words, combined with a handful of model sentence structures, to generate Lorem Ipsum which looks reasonable. The generated Lorem Ipsum is therefore always free from repetition, injected humour, or non-characteristic words etc.',
    nome: 'Luna'
}]

export default function Personagens() {
    const [personagem, setPersonagem] = useState(personagens[0])

    const alterarPersonagem = (personagem) => {
        setPersonagem(personagem)
    }

    return (
        <Box sx={{ padding: '64px', background: 'white' }}>
            <Box sx={{ width: '60%', paddingLeft: '20%' }}>
                <Box sx={{ display: 'flex' }}>
                    {personagens.map((personagem, index) => {
                        return (
                            <Box key={index} sx={{ display: 'flex', justifyContent: 'space-around', marginInline: '8px', cursor: 'pointer' }} onClick={() => alterarPersonagem(personagem)}>
                                <img src={personagem.img} alt="Personagem" style={{ width: '150px' }} />
                            </Box>
                        )
                    })}
                </Box>
                <Box sx={{ display: 'flex' }}>
                    <img src={personagem.imgFull} alt="Personagem" style={{ margin: '8px', height: '500px', width: '200px' }} />
                    <Box sx={{ padding: '64px' }}>
                        <Box sx={{ borderLeft: '2px solid black', paddingLeft: '8px' }}>
                            <Typography variant='h3'>{personagem.nome}</Typography>
                            <Typography>{personagem.texto}</Typography>
                        </Box>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}