import { Box } from "@mui/material";
import { useState } from "react";
import * as Constantes from "../utils/constantes";
import * as Utils from "../utils/utils";
import Toolbar from "../components/toolbar/Toolbar";
import Sobre from "./sobre/Sobre";
import Inicio from "./inicio/Inicio";
import Contato from "./contato/Contato";
import * as Cores from "../utils/cores";

export default function Page(props) {
    const [item, setItem] = useState(Constantes.HOME_INICIO)

    const { height, width } = Utils.useWindowDimensions();
    const isMobile = width < Constantes.MOBILE_SCREEN_SIZE

    const selecionarItem = (item) => {
        setItem(item)
    }

    return (
        <Box sx={{ height: '100%' }}>
            <Box sx={{ height: height, background: Cores.COR_PRINCIPAL_GRADIENT_ALFA, overflowY: 'scroll', ...Utils.barra() }}>
                <Toolbar
                    selecionarItem={selecionarItem}
                    itemSelecionado={item}
                />
                {item === Constantes.HOME_INICIO &&
                    <Inicio />
                }
                {item === Constantes.HOME_SOBRE &&
                    < Sobre />
                }
                {item === Constantes.HOME_CONTATO &&
                    <Contato />
                }
                <Box sx={{height: '300px'}}>

                </Box>
            </Box>

        </Box>
    )
}