import { Box, Typography } from "@mui/material";
import Andando from "../../img/andando.jpg";
import Ataque from "../../img/ataque.jpg";
import Dragao from "../../img/dragao.jpg";
import Header from "../../img/header.jpg";
import * as Cores from "../../utils/cores";
import { useState } from "react";
import useInterval from "../../hooks/useInterval";

const imagens = [
    { img: Andando, texto: 'Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC.' },
    { img: Ataque, texto: 'or randomised words which don look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isn' },
    { img: Dragao, texto: 'e generator on the Internet. It uses a dictionary of over 200 Latin words, combined with a handful of model sentence structures, to generate Lor' },
    { img: Header, texto: 'Lorem Ipsum is therefore always free from repetition, injected humour, or non-' }]

export default function Carrousel(props) {
    const [imagemSelecionada, setImagemSelecionada] = useState(imagens[0])
    const [largura, setLargura] = useState('0%')
    const [contador, setContador] = useState(0)

    useInterval(() => {
        const total = 50000
        if (contador > total) {
            let index = imagens.indexOf(imagemSelecionada)
            index = index === imagens.length - 1 ? 0 : index + 1
            setImagemSelecionada(imagens[index])
            setContador(0)
            setLargura('0%')
        } else {
            setContador(contador + 100)
            setLargura(`${(contador * 100) / total}%`)
        }
    }, 10)

    return (
        <Box sx={{ width: '100%', background: Cores.COR_PRINCIPAL }}>
           
            <Box sx={{ height: '5px', background: Cores.COR_PRINCIPAL_CLARO }}>
                <Box sx={{ height: '5px', background: Cores.COR_PRINCIPAL, width: largura }} />
            </Box>

            <Box sx={{ display: 'flex', justifyContent: 'space-around', cursor: 'pointer', width: '100%' }}>

                <Box sx={{ width: '100%', position: 'relative', }}>
                    <img src={imagemSelecionada.img} alt="Personagem" style={{ width: '100%' }} />
                    <Box sx={{
                        width: '100%', height: '400px',
                        background: Cores.COR_PRETO_FADE_ALFA2,
                        //display: 'flex',
                        position: 'absolute',
                        transform: 'translate(0%, -100%)',
                    }}>
                        <Box sx={{ paddingInline: '10%', display: 'flex', alignItems: 'center', paddingTop: '100px' }}>
                            <Typography variant='h3' sx={{ width: '25%', color: 'white', borderRight: '3px solid white', paddingRight: '32px' }}>
                                Torem Ipsum is not simply random
                            </Typography>
                            <Box>
                                <Typography variant='body1' sx={{ color: 'white', paddingLeft: '32px' }}>
                                    Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC.
                                </Typography>
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}