import { Box } from "@mui/material";
import logoStudio from '../../img/logoStudio.png'
import logo from '../../img/logo.png'
import * as Cores from "../../utils/cores";

export default function Sobre() {
    return (
        <Box sx={{ height: '100%', marginTop: '8%' }}>
            <h1>Sobre</h1>
            <Box sx={{ background: Cores.COR_PRINCIPAL, padding: '64px' }}>
                <img src={logo} alt="Boss" style={{ height: '350px' }} />
            </Box>
            <Box sx={{ background: 'white', padding: '64px' }}>
                <img src={logoStudio} alt="Boss" style={{ height: '350px' }} />
            </Box>
        </Box>
    )
}