import { Box, Typography } from "@mui/material";
import imgFundo from '../../img/fundo.jpeg'
import GifArea from "./GifArea";
import Personagens from "./Personagens";
import Carrousel from "./Carrousel";
import Folhagem from "../../animation/Folhagem";

export default function Inicio() {
    return (
        <Box>
            <Box sx={{
                zIndex: '1',
                position: 'relative',
                textAlign: 'center',
                overflow: 'hidden',
            }}>
                <Folhagem />
                <img style={{}} src={imgFundo} width='100%' />
                <Typography variant='h1' sx={{
                    color: '#754421',
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)'
                }}>GUILD BATTALION</Typography>
            </Box >
            <GifArea />
            <Personagens />
            <Carrousel />

        </Box>
    )
}