import { Box, Typography } from "@mui/material";
import * as Cores from "../../utils/cores";
import Item from "./Item";
import * as Constantes from "../../utils/constantes";
import imgLogo from "../../img/logo.png";

const itens = [Constantes.HOME_INICIO, Constantes.HOME_SOBRE, Constantes.HOME_CONTATO]

export default function Toolbar(props) {
    const { selecionarItem, itemSelecionado } = props

    return (
        <Box sx={{
            zIndex: '5', width: '100%', position: 'fixed', height: '10%', background: Cores.COR_PRETO_FADE_ALFA,
            display: 'flex', justifyContent: 'space-between', paddingTop: '8px', alignItems: 'center'
        }}>
            <Box sx={{ marginLeft: '64px' }}>
                <img src={imgLogo} height='92px' />
            </Box>
            <Box sx={{ display: 'flex', margin: '24px', paddingRight: '64px' }}>
                {itens.map((item, index) => (
                    <Item
                        item={itemSelecionado}
                        selecionarItem={selecionarItem}
                        titulo={item}
                        key={index}
                    />
                ))}
                <a style={{textDecoration: 'none'}} target="_blank" href="https://store.steampowered.com/app/2857420/Guild_Battalion/">
                    <Box sx={{
                        border: 'solid 2px white',
                        padding: '10px',
                        borderRadius: '15px',
                        marginTop: '-11px',
                        cursor: 'pointer',
                        marginLeft: '24px'
                    }}>

                        <Typography sx={{
                            color: 'white'
                        }}>Jogue agora!</Typography>
                    </Box>
                </a>
            </Box>
        </Box>
    )
}