import { useEffect, useState } from 'react';
import * as Cores from './cores';
import * as Constantes from './constantes';

export function checarUsuario(role, valor) {
    if (role === valor) return true
    return false
}

export const capitalize = (str) => {
    return str.charAt(0).toUpperCase() + str.slice(1);
}

export function checarUsuarioAdmin(valor){
    if(checarUsuario(Constantes.ROLE_ADMIN, valor)) return true
    return false
}

export const randomNumber = (min, max, precision) => {
    return Math.floor((min + Math.random() * (max - min)) * precision) / precision
}
export function verificarNoite() {
    const hours = new Date().getHours()
    const isDayTime = hours > 6 && hours < 19;
    if (isDayTime === true) {
        return false
    }
    return true
}
export function dividirArrayEmGrupos(arr, len) {
    if (arr === null || arr === undefined) return []
    var chunks = [], i = 0, n = arr.length;
    while (i < n) {
        chunks.push(arr.slice(i, i += len));
    }
    return chunks;
}

export const montarAnos = (anoReferencia, range) => {
    let anos = []
    let i = 0
    let anoBase = anoReferencia - 0.5 * range
    while (i < range) {
        anos.push(anoBase + i)
        i++
    }
    return dividirArrayEmGrupos(anos, 4)
}

export function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
        width,
        height
    };
}

export function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);
    return windowDimensions;
}

export const verificarUsuarioLogado = (userDetails) => {
    if (userDetails === null || userDetails === undefined || userDetails.id === 0) {
        return false
    }
    return true
}

export const barra = () => {
    return {
        "::-webkit-scrollbar": {
            width: "3px",
            height: '4px'
        },
        "::-webkit-scrollbar-track": {
            backgroundColor: '#fff5f5'
        },
        "::-webkit-scrollbar-thumb": {
            background: Cores.COR_PRINCIPAL,
            borderRadius: "15px",
            height: "1px"
        },
        "::-webkit-scrollbar-thumb:hover": {
            background: Cores.COR_PRINCIPAL,
            maxHeight: "1px"
        }

    }
}
